@import url(../dialog/index.css?__inline);
.storehouse{
	background-image: url(img/bg.png?__sprite);
	width: 1193px;
	height: 584px;
}
.storehouse-title{
	text-indent: -999em;
	width: 99px;
	height: 42px;
	display: inline-block;
	background-image: url(img/title.png?__sprite);
}
.storehouse-tab li{
	width: 180px;
	height: 64px;
	background-image: url(img/li.png?__sprite);
	margin-bottom: 11px;
	font-size: 28px;
	color: #fff;
	text-align: center;
	padding-top: 16px;
	text-shadow: 0px 3px #b26d22,
				 0px -3px #b26d22,
				 3px 0px #b26d22,
				 -3px 0px #b26d22;
	
}
.storehouse-tab li.active{
	background-image: url(img/li-active.png?__sprite);
	text-shadow: 0px 3px #b81b1d,
				 0px -3px #b81b1d,
				 3px 0px #b81b1d,
				 -3px 0px #b81b1d;
}
.storehouse-r1{
	display: flex;
	padding-top: 81px;
	padding-left: 38px;
}
.storehouse-r1c2{
	width: 933px;
	height: 483px;
	background-image: url(img/main.png?__sprite);
	margin-left: 14px;
}
.storehouse-list{
	overflow: hidden;
	padding-left: 20px;
	padding-top: 17px;
}
.storehouse-list li{
	float: left;
	width: 108px;
	height: 108px;
	text-align: center;
	margin-right: 4px;
	margin-bottom: 4px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.storehouse-list img{
	display: block;
}
